.App {
  font-family: sans-serif;
  text-align: center;
}
button {
  padding: 1.5rem;
  margin: 2rem;

  border-width: 3px;
  border-color: rgb(12, 240, 80);
  border-radius: 1rem;
}
.red {
  border-color: rgb(240, 46, 12);
}
button:hover {
  background: #000;
  background: rgb(12, 240, 80);
}
.red:hover {
  background-color: rgb(240, 46, 12);
}
